/* 字体大小 */
.main {
  padding: 1rem .2rem;
  background: #fff; }

.article-header {
  padding: .5rem 0; }
  .article-header .article-title {
    font-size: .42rem; }
  .article-header .article-info {
    font-size: 0.24rem;
    color: #888f9b; }
  .article-header .replies,
  .article-header .visits {
    margin-right: .2rem; }
  .article-header .iconfont {
    font-size: 0.28rem;
    margin-right: .1rem; }

.markdown-text {
  font-size: 0.3rem; }
  .markdown-text h1 {
    display: none; }
  .markdown-text p {
    margin: .3rem 0;
    color: #666; }
  .markdown-text h2 {
    margin-top: .2rem;
    line-height: .8rem;
    font-weight: bold;
    font-size: 0.34rem;
    border-bottom: 1px solid #eee; }
  .markdown-text h3 {
    margin: .3rem 0;
    font-weight: bold;
    font-size: 0.32rem; }
  .markdown-text ul {
    margin: .3rem 0; }
  .markdown-text li:before {
    content: '';
    margin-right: .1rem;
    display: inline-block;
    vertical-align: middle;
    width: .1rem;
    height: .1rem;
    border-radius: 50%;
    background: #333; }
  .markdown-text a {
    color: #4577dc; }

.comments .comments-header {
  font-size: 0.36rem;
  line-height: .9rem;
  padding: 0 .2rem; }
  .comments .comments-header span {
    color: #666; }

.comments .item {
  position: relative;
  padding: .3rem .2rem; }

.comments .item-body {
  padding-left: .8rem; }

.comments .user-avatar {
  position: absolute;
  left: .2rem;
  top: .3rem;
  width: .6rem;
  height: .6rem;
  border-radius: 50%;
  overflow: hidden;
  background: #acb2c1; }

.comments .user-name {
  font-size: 0.32rem;
  line-height: 1; }

.comments .create-at {
  font-size: 0.24rem;
  color: #888f9b; }

.comments .markdown-text {
  padding: .2rem 0; }
  .comments .markdown-text p {
    margin: 0;
    color: #333; }

.comments .operation {
  text-align: right;
  color: #666; }

.comments .like,
.comments .reply {
  display: inline-block;
  height: .8rem;
  margin-left: .2rem; }

.comments .icon-fanhui {
  position: relative;
  top: .06rem;
  font-size: .45rem; }

.reply-wrapper .textarea {
  height: 1.5rem;
  padding: .1rem; }
  .reply-wrapper .textarea textarea {
    width: 100%;
    height: 100%;
    font-size: 0.28rem; }

.reply-wrapper .reply-btn {
  margin-top: .3rem;
  text-align: right; }
  .reply-wrapper .reply-btn a {
    display: inline-block;
    width: 1rem;
    height: .6rem;
    line-height: .6rem;
    color: #fff;
    text-align: center;
    background: #4577dc;
    border-radius: .04rem; }

.comments .comments-footer {
  padding: 1rem .2rem 1rem .2rem; }
